@import "../utils.scss";
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap");
.login {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  &__pic {
    cursor: pointer;
    height: 75%;
    width: 50%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    background-image: url("https://images.unsplash.com/photo-1623593688280-a5aec8ac4ae7?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=387&q=80");
  }
}

.login__form {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  text-transform: capitalize;
  z-index: 100;
  position: absolute;
  top: 0;
  right: 0;
  width: 10vmax;
  height: 100%;
  font-size: 1.5vmax;
  .login__wrap {
    display: flex;
    flex-direction: column;
    height: 50%;
    width: 90%;
    margin: auto;
    text-align: center;
    .signIn {
      --Width: 0;
      line-height: 3;
      cursor: pointer;
      &::after {
        position: relative;
        left: 0;
        width: var(--Width);
        height: 1px;
        background: rgb(226, 226, 226);
        content: "";
        display: block;
      }
    }
    .closeForm {
      opacity: 0;
      cursor: pointer;
      font-size: 1.5vmax;
      position: absolute;
      right: 5%;
      line-height: 3;
    }
    input {
      opacity: 0;
      background: whitesmoke;
      border: none;
      text-align: center;
      cursor: pointer;
      font-size: 1.5vmax;
      line-height: 3;
      &:not(.submit) {
        border-bottom: 1px solid rgb(196, 196, 196);
      }
      &:hover,
      :focus {
        &::placeholder {
          border: none;
          opacity: 0;
        }
      }
      &::placeholder {
        font-size: 1.5vmax;
      }
      &:valid + .submit {
        font-weight: 500;
      }
    }
    .submit {
      font-size: 1.7vmax;
      font-family: "Roboto", sans-serif;
      text-align: center;
      font-weight: 300;
      cursor: pointer;
      &:hover,
      :focus {
        font-weight: 500;
      }
    }
  }
}
