@import "../utils.scss";

nav {
  @include tablet {
    height: 5vw;
  }
  position: fixed;
  width: 100vw;
  height: 2vw;
  z-index: 300;
  .link {
    top: 50%;
    position: absolute;
    height: 300%;
    width: 10%;
    div {
      margin-left: 10%;
      width: 100%;
      height: 100%;
      background: url("./images/logo.png") no-repeat;
      background-size: contain;
    }
  }
  .buttons {
    display:flex;
    justify-content: flex-end;
    width: 50%;
    position: absolute;
    right: 2%;
    .fav {
      margin: 0 5%;
      font-family: "Roboto", sans-serif;
      font-weight: 300;
      text-transform: capitalize;
      cursor: pointer;
      font-size: 1.5vmax;
      line-height: 3;
    }
    .logout {
      margin: 0 5%;
      font-family: "Roboto", sans-serif;
      font-weight: 300;
      text-transform: capitalize;
      cursor: pointer;
      font-size: 1.5vmax;
      line-height: 3;
    }
  }
}
